@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
h1,h2,h3,h4,h5,h6,textarea{font-family:"Poppins",sans-serif}p,div{font-family:"Montserrat",sans-serif}.fancy{font-family:"BEYNO",sans-serif}.photo-grid{display:grid;grid-gap:1rem;gap:1rem;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));grid-auto-rows:240px}.container{width:80%;margin:0 auto}h1{font-family:"Poppins",sans-serif;font-size:56px;font-weight:bold;text-align:center;letter-spacing:5px}.container h2{text-align:center;letter-spacing:5px;font-weight:300;font-family:"Poppins",sans-serif;margin-top:5rem;font-size:32px}.glimpse-wrapper{margin-bottom:5rem}@media screen and (min-width: 600px){.card-tall{grid-row:span 2 / auto}.card-wide{grid-column:span 2 / auto}}body{background:#131313;color:#fff;font-family:'Poppins', sans-serif}.card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background:#353535;font-size:3rem;color:#fff;box-shadow:rgba(3,8,20,0.1) 0px 0.15rem 0.5rem,rgba(2,8,20,0.1) 0px 0.075rem 0.175rem;width:100%;border-radius:4px;transition:all 500ms;overflow:hidden;aspect-ratio:16/9;background-size:cover;background-position:center;background-repeat:no-repeat}.card:hover{box-shadow:rgba(2,8,20,0.1) 0px 0.35em 1.175em,rgba(2,8,20,0.08) 0px 0.175em 0.5em;-webkit-transform:translateY(-3px) scale(1.1);transform:translateY(-3px) scale(1.1)}.video{display:-webkit-flex;display:flex;grid-gap:1rem;gap:1rem;position:relative}.card-image{width:auto;aspect-ratio:16/9;height:100%}.tedx-iframe{left:0;top:0;display:-webkit-flex;display:flex}.single-video{position:relative}

